import axios from "axios";
import React, { useState } from "react";

function Login({ history }) {
  const [val, setVal] = useState({
    username: "",
    password: "",
  });
  const [err, setErr] = useState(false);
  const handleChange = (e) => {
    setErr(false);
    setVal({ ...val, [e.target.name]: e.target.value });
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    const config = {
      headers: {
        "content-type": "application/json",
      },
    };
    axios
      .post("/api/users/login/", val, config)
      .then((res) => {
        console.log(res);
        localStorage.setItem("token", res.data.access);
        localStorage.setItem(
          "name",
          res.data.first_name + " " + res.data.last_name
        );
        localStorage.setItem("department", res.data.department);
        localStorage.setItem("image", res.data.picture);
        history.push("/voting/0");
      })
      .catch((err) => {
        setErr(true);
      });
  };
  return (
    <div className="bg" style={{ backgroundImage: "url(/static/bg-img.jpg)" }}>
      <h1
        className="text-center py-2 container"
        style={{ fontFamily: "cursive" }}
      >
        Welcome to BIC society Election E-voting system
      </h1>

      <div className="container">
        <div className="center" style={{ marginTop: "120px" }}>
          <form
            onSubmit={handleSubmit}
            className="bg-white px-4 py-4"
            style={{ opacity: "0.8", width: "25rem" }}
          >
            <h4 className="text-center">Login</h4>
            <div className="form-group my-2">
              <input
                type="text"
                className="form-control"
                onChange={handleChange}
                id="formGroupExampleInput"
                placeholder="Username"
                name="username"
              />
            </div>
            <div className="form-group">
              <input
                type="password"
                className="form-control"
                id="formGroupExampleInput2"
                onChange={handleChange}
                placeholder="Password"
                name="password"
              />
            </div>
            <div className="text-center">
              <button type="submit" className="btn btn-primary mt-1">
                Login
              </button>
            </div>
          </form>
          {err && (
            <div className="alert alert-danger mt-2" role="alert">
              No Active Account With this credentials
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Login;
