import React from "react";
import { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
function Votting({ match }) {
  const history = useHistory();
  const parmas = Number(match.params.id);
  const [ids, setIds] = useState([]);
  const [society, setSociety] = useState([]);
  const [err, setErr] = useState(false);
  const [state, setState] = useState("change me");
  const [nextInfo, setNextInfo] = useState({
    i: "",
    j: "",
  });
  const handleChange = (i, j) => {
    setState("change you");
    const body = {
      candidate_id: j,
    };
    setNextInfo({
      i: i,
      j: j,
    });
    const config = {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    };
    axios.post("/api/voting/candidate/", body, config).then((res) => {
      if (res.data === "You Vote already") {
        setErr(true);
      } else {
        if (parmas + 1 == ids.length) {
          axios.get("/users/unactive", config);
          localStorage.removeItem("token");
          localStorage.removeItem("name");
          localStorage.removeItem("image");
          localStorage.removeItem("department");
          history.push("/thanks");
          return 0;
        } else {
          history.push(`/voting/${ids[parmas + i]}`);
        }
      }
    });
  };

  const next_val = () => {
    const body = {
      candidate_id: nextInfo.j,
    };
    const config = {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    };
    axios.post("/api/voting/candidate/", body, config).then((res) => {
      if (parmas + 1 == ids.length) {
        axios.get("/users/unactive", config);
        localStorage.removeItem("token");
        localStorage.removeItem("name");
        localStorage.removeItem("image");
        localStorage.removeItem("department");
        history.push("/thanks");
        return 0;
      } else {
        history.push(`/voting/${ids[parmas + nextInfo.i]}`);
      }
    });
  };
  // getting all societies ids.

  useEffect(() => {
    if (!localStorage.getItem("token")) {
      history.push("/");
    } else {
      axios.get("/api/voting/getsocities").then((res) => {
        let arr = [];

        for (let i = 0; i < res.data.length; i++) {
          arr.push(res.data[i].id);
        }
        setIds([...arr]);
        axios
          .get(`/api/voting/society/${arr[parmas]}`)
          .then((res) => setSociety(res.data));
      });
    }
  }, [history, state, history.location]);
  return (
    <div className="row">
      <div className="col-md-3 col-xs-12 border">
        <div className="py-3 ">
          <h4 className="text-center">Profile</h4>
          <img
            className="px-2 my-2 img-fluid"
            style={{ objectFit: "cover" }}
            // width="220"
            // height="220"
            src={localStorage.getItem("image")}
            alt=""
          />
          <h4 className="ms-2"> Name: <strong> {localStorage.getItem("name")}</strong> </h4>
          <h4 className="ms-2"> Department: <strong> {localStorage.getItem("department")} </strong></h4>
        </div>
      </div>
      <div className="col-md-9 col-xs-12">
        <h1 className="text-center py-2 text-uppercase">
          {society && society.name}
        </h1>
        <h2 className="text-center">Select Any Candidate</h2>
        <div className="my-2 container text-center">
          <form className="row">
            {society &&
              society.candidate &&
              society.candidate.map((v, i) => (
                <div className="col-md-6 col-xs-12 my-1" key={i}>
                  {console.log(v)}
                  <div className="card mx-auto" style={{ width: "18rem" }}>
                    <img
                      className="card-img-top"
                      src={v.image}
                      alt="Card image cap"
                    />
                    <div className="card-body py-1">
                      <div className="form-check">
                        <input
                          className="form-check-input"
                          type="radio"
                          onChange={() => handleChange(i, v.id)}
                          name={`exampleRadios${i}`}
                          id={`exampleRadios${i}`}
                          value="option1"
                          checked={false}
                        />
                        <label className="card-text" htmlFor={`exampleRadios${i}`}>
                          {v.member_name}
                        </label>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            {err && (
              <div>
                <div className="alert alert-danger mt-2 mx-5" role="alert">
                  You voted Already this candidate
                </div>
                <button
                  onClick={next_val}
                  type="button"
                  className="btn btn-primary"
                >
                  Next
                </button>
              </div>
            )}
          </form>
        </div>
      </div>
    </div>
  );
}

export default Votting;
