import React, { useEffect } from "react";

function Thanks({ history }) {
  useEffect(() => {
    setTimeout(() => {
      history.push("/");
    }, 5000);
  }, []);
  return (
    <div className="container">
      <div className="text-center">
        <img className="img-fluid" src="/static/logo.jpeg" />
      </div>
      <div className="d-flex mt-1 justify-content-center flex-wrap">
        <div className="mx-3">
          <img
            style={{ objectFit: "contain", height: "350px" }}
            src="/static/1.jpeg"
            alt=""
          />
          <h4 className="text-center">
            <strong>CEO </strong> <br /> Mr. Nasir Kasuri{" "}
          </h4>
        </div>

        <div className="mx-3">
          <img
            style={{ objectFit: "contain", height: "350px" }}
            src="/static/2.jpeg"
            alt=""
          />
          <h4 className="text-center">
            {" "}
            <strong>CGM</strong>
            <br /> Miss Raana Sarmad{" "}
          </h4>
        </div>

        <div className="mx-2">
          <img
            style={{ objectFit: "contain", height: "350px" }}
            src="/static/3.jpg"
            alt=""
          />
          <h4 className="text-center">
            <strong>Rector </strong>
            <br /> Mr. Aamir Ikhlaq{" "}
          </h4>
        </div>
      </div>
      <div className="text-center mt-4">
        <h1>Thank you for casting your vote</h1>
      </div>
    </div>
  );
}

export default Thanks;
